import _ from 'lodash'

function addressObject(options) {
    return _.merge({
        fieldId: '',
        name: '',
        field: '',
        label: '',
        multiple: false,
        placeholder: '',
        showDetail: false,
        value: {
            province: {
                name: '',
                value: '110000'
            },
            city: {
                name: '',
                parent: '110000',
                value: '110100'
            },
            area: {
                name: '',
                parent: '110100',
                value: '110101'
            },
            detail: ''
        },
        rules: {},
        makeStyle: {  // 
            active: false
        },
        style: {},  // 
        setting: {}
    }, options)
}

export default addressObject