import _ from 'lodash'

function inputObject(options) {
	return _.merge({
		fieldId: '',
		name: '',
		label: '',
		placeholder: '',
		value: '',
		maxlength: '',
		showWordLimit: false,
		rule: '',
		filter: '',
		isHaveUnit: false,
		unitName: '',
		/**
		 * {
		 *  'required': {required: true, message: '...', trigger: 'blur'},
		 *  'minMax': {...}
		 * }
		 */
		rules: {},
		makeStyle: {  // 
			active: false
		},
		style: {},    // 
		setting: {},  // rows: 2
	}, options)
}

export default inputObject