<template>
	<div>
		<item-box classSuffix="input">
			<el-form-item label="">
				<el-input v-model="data.label"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="data.field"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="data.value"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="data.placeholder"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="data.maxlength"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="12">
					<el-switch v-model="data.showWordLimit"
							   active-text=""
							   inactive-text="">
					</el-switch>
				</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="12">
					<el-switch v-model="data.isHaveUnit"
							   active-text=""
							   inactive-text=""
							   :disabled="data.setting.isHaveSuffixComponent">
					</el-switch>
				</el-col>
				<el-col :span="12">
					<el-input v-model="data.unitName"
							  v-if="data.isHaveUnit"
							  placeholder="">
					</el-input>
				</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-switch v-model="data.setting.isHavePrefixIcon"
						   @change="isHavePrefixIconChange"
						   active-text=""
						   inactive-text="">
				</el-switch>
				<el-input v-model="data.setting.prefixIcon"
						  v-if="data.setting.isHavePrefixIcon">
					<el-button slot="append"
							   @click="openSelectIcon('prefixIcon')"></el-button>
				</el-input>
			</el-form-item>
			<el-form-item label="">
				<el-switch v-model="data.setting.isHaveSuffixIcon"
						   @change="isHaveSuffixIconChange"
						   active-text=""
						   inactive-text="">
				</el-switch>
				<el-input v-model="data.setting.suffixIcon"
						  v-if="data.setting.isHaveSuffixIcon">
					<el-button slot="append"
							   @click="openSelectIcon('suffixIcon')"></el-button>
				</el-input>
			</el-form-item>
			<el-form-item label="">
				<el-switch v-model="required"
						   active-text=""
						   inactive-text=""></el-switch>
			</el-form-item>
			<el-form-item label=""
						  v-if="required">
				<el-input v-model="data.rules.required.message"
						  placeholder=""></el-input>
			</el-form-item>
			<el-form-item label=""
						  v-if="required">
				<el-select v-model="data.filter"
						   placeholder=""
						   style="width: 100%">
					<el-option v-for="(i, index) in filters"
							   :key="'rules' + index"
							   :label="i.label"
							   :value="i.value">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label=""
						  v-if="required">
				<el-select v-model="data.rule"
						   placeholder=""
						   style="width: 100%">
					<el-option v-for="(i, index) in rules"
							   :key="'rules' + index"
							   :label="i.label"
							   :value="i.value">
					</el-option>
				</el-select>
			</el-form-item>
			<!-- <regx-add v-model="data.rules"></regx-add> -->
		</item-box>

		<select-icon ref="select_icon"
					 @select-icon="selectIconItem"></select-icon>
	</div>
</template>

<script>
import ItemBox from '../common/ItemBox'
import SelectIcon from '../common/select-icon'
import RegxAdd from '../common/regx-add'
import Mixins from '../common/mixins'

export default {
	name: 'InputConfig',
	mixins: [Mixins],
	components: { ItemBox, SelectIcon, RegxAdd },
	data() {
		return {
			currentProp: '',
			rules: [
				{ label: '', value: '' },
				{ label: '', value: 'number' },
				{ label: '', value: 'email' },
				{ label: '', value: 'idCard' },
				{ label: '', value: 'phone' },
				{ label: '', value: 'mobilePhone' },
				{ label: '', value: 'pwd' },
				{ label: '', value: 'postal' },
				{ label: '', value: 'upper' },
				{ label: '', value: 'lower' }
			],
			filters: [
				{ label: '', value: '' },
				{ label: '', value: 'keepInt' },
				{ label: '', value: 'removeAllSpecial' },
				{ label: '', value: 'removeSpecialChar' },
				{ label: '', value: 'removeSpace' },
				{ label: '', value: 'removePunctuation' },
				{ label: '', value: 'removeChinese' },
				{ label: '', value: 'keepChinese' },
				{ label: '', value: 'removeEnglish' },
				{ label: '', value: 'keepEnglish' },
			]
		}
	},
	props: {
		data: {
			type: Object,
			required: true
		}
	},
	watch: {
		data(v) {
			this.required = v.rules.required ? v.rules.required.required : false
		}
	},
	methods: {
		openSelectIcon(prop) {
			this.currentProp = prop
			this.$refs.select_icon.open()
		},

		selectIconItem(item) {
			if (this.currentProp) {
				this.data.setting[this.currentProp] = item
			}
		},

		isHavePrefixIconChange(val) {
			if (!val) {
				this.data.setting.prefixIcon = ''
			}
		},

		isHaveSuffixIconChange(val) {
			if (!val) {
				this.data.setting.suffixIcon = ''
			}
		}
	}
}
</script>