import _ from 'lodash'

function cascaderObject(options) {
    return _.merge({
        fieldId: '',
        name: '',
        field: '',
        label: '',
        multiple: false,
        placeholder: '',
        value: options.multiple ? [] : '',
        rules: {},
        makeStyle: {  // 
            active: false
        },
        style: {},  // 
        setting: {}
    }, options)
}

export default cascaderObject