import _ from 'lodash'

function selectObject(options) {
	return _.merge({
		fieldId: '',
		name: '',
		field: '',
		label: '',
		multiple: false,
		placeholder: '',
		value: options.multiple ? [] : '',
		selections:
			options.selections === 'unset'
				? undefined
				: Object.prototype.toString.call(options.selections) === '[object Array]' ? options.selections : [{ label: '', value: '1' }, { label: '', value: '2' }],
		rules: {},
		makeStyle: {  // 
			active: false
		},
		style: {},  // 
		setting: {}
	}, options)
}

export default selectObject